import { TemplateType, ItemType } from '../types/types';

export const sectionTemplate = {
  inputContainer(): TemplateType {
    return `
      <section class="input-container">
      <h2 hidden>  </h2>
      <form id="add-item-form">
        <label>   .</label>
        <div>
          <input id="add-item-name" class="add-item-input" placeholder="" />
          <input id="add-item-price" class="add-item-input" placeholder="" type="number"/>
          <input id="add-item-quantity" class="add-item-input" placeholder="" type="number"/>
          <button class="submit-button"></button>
        </div>
      </form>
      </section>
    `;
  },

  tableContainer(items: ItemType[]): TemplateType {
    return `
    <section class="table-container">
      <h2> </h2>
      <table class="item-table">
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
         ${items.map(item => this.normalTableContainer(item)).join('')}
      </table>
    </section>
    `;
  },

  normalTableContainer(item: ItemType): TemplateType {
    return `
    <tr>
      <td class="table-item-name">${item.name}</td>
      <td class="table-item-price">${item.price}</td>
      <td class="table-item-quantity">${item.quantity}</td>
      <td class="item-table-button-container">
        <button class="item-table-change-button"></button>
        <button class="item-table-delete-button"></button>
      </td>
    </tr>
    `;
  },

  changeTableContainer(item: ItemType) {
    return `
    <tr>
      <td><input class="table-item-input table-item-input-name" value="${item.name}" placeholder=""></input></td>
      <td><input class="table-item-input table-item-input-price" value="${item.price}" placeholder="" type="number"></input></td>
      <td><input class="table-item-input table-item-input-quantity" value="${item.quantity}" placeholder="" type="number"></input></td>
      <td>
       <button class="item-table-confirm-button"></button>
      </td>
    </tr>
`;
  },
};

export const manageItemTemplate = (items: ItemType[]): TemplateType => `
  ${sectionTemplate.inputContainer()}
  ${sectionTemplate.tableContainer(items)}
`;
