import { COINS } from '../constants/constants';
import { CoinsType, ItemType, TemplateType } from '../types/types';

const sectionTemplate = {
  inputContainer(inputMoney: number): TemplateType {
    return `
    <section class="input-container">
      <h2 hidden> </h2>
      <form>
        <label>   </label>
        <div>
          <input class="charge-money-input" placeholder="" />
          <button class="submit-button"></button>
        </div>
      </form>
      <p> : <span>${inputMoney}</span></p>
    </section>
    `;
  },

  itemTableContainer(items: ItemType[]): TemplateType {
    return `
    <section class="table-container">
      <h2>   </h2>
      <table class="item-table">
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
        ${items
          .map(item => {
            return `
          <tr>
            <td>${item.name}</td>
            <td>${item.price}</td>
            <td>${item.quantity}</td>
            <td>
              <button class="item-table-purchase-button"></button>
            </td>
          </tr>`;
          })
          .join('')}
      </table>
    </section>
    `;
  },

  coinTableContainer(coins: CoinsType): TemplateType {
    return `
    <section class="table-container">
      <h2> </h2>
      <table class="coin-table">
          <tr>
            <th></th>
            <th></th>
          </tr>
          ${Object.keys(coins)
            .map(coinKey => {
              return `
              <tr>
                <td>${COINS[coinKey]}</td>
                <td>${coins[coinKey]}</td>
              </tr>
            `;
            })
            .join('')}
        </table>
      <button class="return-money-button"></button>
    </section>
    `;
  },
};

export const purchaseItemTemplate = (
  items: ItemType[],
  coins: CoinsType,
  inputMoney: number,
): TemplateType => `
  ${sectionTemplate.inputContainer(inputMoney)}
  ${sectionTemplate.itemTableContainer(items)}
  ${sectionTemplate.coinTableContainer(coins)}
`;
